
import XImg from 'vux/src/components/x-img/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default {
data() {
return {
account: 'wangjie',
pass: '12345678'
};
},
components: {
XImg,
Icon
},
methods: {
login() {
if (!this.account)
return this.$vux.toast.show({
text: '请输入帐号',
type: 'text',
position: 'top'
});
if (!this.pass)
return this.$vux.toast.show({
text: '请输入密码',
type: 'text',
position: 'top'
});
this.$store
.dispatch('login', {
name: this.account,
password: this.pass,
openId: this.$route.query.openId || undefined
})
.then(
result =>
this.$router.push({
path: '/'
}),
error =>
this.$vux.toast.show({
text: error.message,
width: '18em',
type: 'text',
position: 'top'
})
);
}
}
};



